*{
    margin: 0;
    padding: 0;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    box-sizing: border-box;
}

.home-container {
    width: 100%;
    height: 100vh;
    background: linear-gradient(to left, #4A00E0, #8E2DE2);
    background-position: center;
    background-size: cover;
    position: relative;
}

.home-form-box {
    width: 96%;
    border-radius: 10px;
    position: absolute;
    top: 18%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #000b2b;
    padding: 50px 50px 60px;
    text-align: center;
}

.home-form-box h1 {
    font-size: 30px;
    margin-bottom: 10px;
    color: #949bdf;
    position: relative;
}

.home-form-box h2 {
    font-size: 20px;
    margin-bottom: 20px;
    color: #949bdf;
    position: relative;
}

.home-button {
    width: 100%;
    margin: 0px 0px;
    display: flex;
    position: relative;
    justify-content: center;
}
.home-button a {
    flex-basis: 10%;
    background: #4A00E0;
    color: #000b2b;
    height: 20px;
    border-radius: 3px;
    border: 0;
    outline: 0;
    margin-bottom: 0;
    cursor: pointer;
    transition: background 0.5s;
}
.home-button a:hover {
    flex-basis: 10%;
    background: #8E2DE2;
    color: #ffffff;
    height: 20px;
    border-radius: 3px;
    border: 0;
    outline: 0;
    margin-bottom: 0;
    cursor: pointer;
    transition: background 0.5s;
}